.messagePage{}
.messagePage .list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.messagePage .list div.li .time{
    text-align: center;
    line-height: 30px;
    margin-top: 10px;
    color: #999;
}
.messagePage .list div.li .cont{
    background: #FFF;
    border-radius: 6px;
    margin: 0 20px;
    padding: 15px;
    -webkit-box-shadow: 1px 2px 2px 2px rgba(200,200,200,.1);
