    display: flex;
    display: -webkit-flex;
    -ms-flex-align: center;
        align-items: center;
    -webkit-align-items: center;
    width: 100%;
    padding-right:  20px;
}
.fillerform .ant-input-group,
.fillerform .ant-btn.ant-btn-primary{
    width: auto;
    margin-left:10px;
}
.warningsearch {
    -ms-flex-positive:1;
        flex-grow:1;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
}
.warningPage .appbar .title{
    color:  #FFF;
    -ms-flex-positive:1;
