.formvalidation.form_input{
	position: relative;
	-ms-flex-item-align: stretch;
	    align-self: stretch;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-negative: 1;
	    flex-shrink: 1;
	justify-self: stretch;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.formvalidation.form_input input{
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-item-align: stretch;
	    align-self: stretch;
	border-radius: 0;
}
.formvalidation.form_input.warning input{
	color: #C00;
}
.warning .warningtext{
	display: inline-block;
	width: 20px;
	height: 20px;
	background: #C00;
	color: #FFF;
	font-size: 16px;
	line-height: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	border-radius: 20px;
	margin: 0 10px;
}
.weui-cell_select .weui-cell__bd:after{
	position: relative;	
}


