	max-width: 86%;
	margin: auto;
	text-align: center;
	margin-bottom: 10px;
}
.weui-toast.toastLetter{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-size: 30px;
    width: 5em;
    min-height: 5em;
    margin-left: -2.5em;
}

.toastDialogContent {
    display: -ms-flexbox;
   	display: flex;
   	display: -webkit-flex;
	-ms-flex-direction:column;
	    flex-direction:column;
}
.toastDialogContent .fa{
	font-size: 80px;
	text-align: center;
}
.toastDialogContent span{
	text-align: center;
	color: #111;
}


